import React, {Component} from 'react';
import FullCalendar from '@fullcalendar/react'; 
import dayGridPlugin from '@fullcalendar/daygrid'; 
import '../App.css';
import 'bootstrap/dist/css/bootstrap.min.css';

export default class EventCalendar extends Component {
  render() {
    return  (  
    <>
    <div className="p-3 m-5 border ">
      <h1 className="text-center mt-5">School Calendar</h1>
      <FullCalendar
       defaultView="dayGridMonth" plugins={[dayGridPlugin]}
       events={[
        { title: 'Start of School Year', date: '2021-08-01' },
        { title: 'Ninoy Aquino Day', date: '2021-08-21' },
        { title: 'National Heroes Day', date: '2021-08-31' },
        { title: 'PTA Assembly', date: '2021-08-06' }
      ]}/>
</div>

     </>
    )
  }
}
